// import dependencies
let PROTOCOL = 'http://';
let BASE_URL = 'localhost:8080';
// let BASE_URL = location.host;
let CHAT_BOX_WIDTH = 380;
let USER_LIST_WIDTH = 300;
let MAX_CHATBOX = Math.floor((screen.width - USER_LIST_WIDTH) / CHAT_BOX_WIDTH);

export class Charybdis {}

Charybdis.User = class {

    constructor() {
    }

    setUserId(userId) {
        this.userId = userId;
    }

    setUsername(username) {
        this.username = username;
    }

    resolve() {
        httpGetAsync(PROTOCOL + BASE_URL + '/api/user/v1/' + this.username)
            .then(response => {
                response.json().then(data => {
                    this.userId = data.userId;
                    this.username = data.username;
                });
            })
            .catch(err => alert('Error when fetching user: ', err));
    }
}

Charybdis.Session = class {

    constructor(elem, uri, currentUser) {
        this.elem = elem;
        this.uri = uri;
        this.currentUser = currentUser;
        this.isLive = false;
        this.sideBar = null;
        this.chatBoxes = [];
    }

    init() {
        this.connect();
        setInterval(() => {
            if (!this.isOpen()) {
                this.connect();
                if (this.isOpen()) {
                    // Reconnect components
                    this.connectSideBar(this.sideBar);
                    this.chatBoxes.forEach((chatBox) => {
                        this.connectChatBox(chatBox);
                        chatBox.removeLoader();
                    });
                }
            }
        }, 5000);
    }

    connect() {
        this.socket = new CharybdisWebSocket(this.uri, this.currentUser.userId,);
        this.socket.bind('open', () => {
            console.log('Connection established');
            this.chatBoxes.forEach((chatBox) => chatBox.removeLoader());
        });
        this.socket.bind('close', () => {
            console.log('Connection closed');
            this.chatBoxes.forEach((chatBox) => chatBox.displayLoader());
        });
        this.socket.bind('error', (err) => {
            this.chatBoxes.forEach((chatBox) => chatBox.displayLoader());
        });
    }

    bind(event, callback) {
        this.socket.bind(event, callback);
    }

    isOpen() {
        return !!this.socket && this.socket.isOpen();
    }

    sendChatMessage(event, message) {
        // console.log('Sending message: ', JSON.stringify(message));
        if (this.isOpen()) {
            this.socket.send(event, message);
        } else {
            console.error('Error: Socket is closed');
        }
    }

    addChatBox(chatBox) {
        this.chatBoxes.push(chatBox);
        this.connectChatBox(chatBox);
    }

    connectChatBox(chatBox) {
        chatBox.removeLoader();
        this.bind('chat-message', (message) => {
            if (message.chatRoomId === chatBox.chatRoomId) {
                // console.log('Received Message: ', message);
                chatBox.newChatMessage(message, 0);
            }
        });
    }

    addSideBar(sideBar) {
        this.sideBar = sideBar;
        this.connectSideBar(sideBar);
    }

    connectSideBar(sideBar) {
        this.bind('online-users', (data) => {
            // console.log(data);
            let users = data.users || [];
            sideBar.users = users.filter(user => user.userId != this.currentUser.userId);
            sideBar.renderOnlineUsers();
        });
    }
}

class CharybdisWebSocket {
    constructor(uri, username) {
        let url = 'ws://' + uri +'/chat/';
        this.conn = new WebSocket(url + username);
        // dispatch to the right handlers
        this.conn.onmessage = (evt) => {
            var json = JSON.parse(evt.data);
            this.dispatch(json.event, json.payload);
        };
        this.conn.onclose = () => this.dispatch('close', null);
        this.conn.onopen = () => this.dispatch('open', null);
        this.conn.onerror = (err) => this.dispatch('error', err);
        this.callbacks = {};
    }
  
    bind(event_name, callback){
        this.callbacks[event_name] = this.callbacks[event_name] || [];
        this.callbacks[event_name].push(callback);
        return this;// chainable
    }
  
    send(event_name, event_data){
        var payload = JSON.stringify({event:event_name, payload: event_data});
        this.conn.send( payload ); // <= send JSON data to socket server
        return this;
    }
  
    dispatch(event_name, payload){
        var chain = this.callbacks[event_name];
        if(typeof chain == 'undefined') return; // no callbacks for this event
        for(var i = 0; i < chain.length; i++){
            chain[i](payload)
        }
    }

    isOpen() {
        return !!this.conn && this.conn.readyState === 1;
    }

}

Charybdis.SideBar = class {
    constructor(elem, session){
        this.elem = elem;
        this.session = session;
        this.currentUser = this.session.currentUser;
        this.chatBoxes = new Map();
        this.onlineUsers = [];
        this.chatGroups = [];
    }

    render() {
        let sidebarDiv = document.createElement('div');
        sidebarDiv.classList.add('charybdis-sidebar');

        // CURRENT USER
        let currentUserDiv = document.createElement('div');
        currentUserDiv.classList.add('current-user');
        currentUserDiv.innerHTML = '<span>' + this.currentUser.username + '</span>';
        sidebarDiv.appendChild(currentUserDiv);

        // SEARCH BAR
        let searchBarDiv = document.createElement('div');
        searchBarDiv.classList.add('search-user');
        searchBarDiv.innerHTML = 
            `<input autocomplete="off" class="sidebar-search-input" placeholder="Search User" spellcheck="false" type="text" aria-label="Search User" value="">
            <i class="clearable_clear">&times;</i>`;
        sidebarDiv.appendChild(searchBarDiv);
        let searchInputEl = searchBarDiv.getElementsByClassName('sidebar-search-input')[0];
        let clearInputEl = searchBarDiv.getElementsByClassName('clearable_clear')[0];
        searchInputEl.oninput = () => {
            let filter = searchInputEl.value;
            this.search(filter);
            if (!!filter) {
                clearInputEl.style.display = 'block';
            } else {
                clearInputEl.style.display = 'none';
            }
        };
        clearInputEl.onclick = () => {
            this.search('');
            searchInputEl.value = '';
            clearInputEl.style.display = 'none';
        };

        // ONLINE USERS
        let onlineUsersDiv = document.createElement('div');
        onlineUsersDiv.classList.add('online-users');
        onlineUsersDiv.innerHTML = '<span class="section-title">online users</span>';
        let onlineUsersUl = document.createElement('ul');
        this.onlineUsersUl = sidebarDiv.appendChild(onlineUsersUl);
        onlineUsersDiv.appendChild(this.onlineUsersUl);
        sidebarDiv.appendChild(onlineUsersDiv);

        // GROUP CONVERSATIONS
        let conversationsDiv = document.createElement('div');
        conversationsDiv.classList.add('group-conversation-list');
        conversationsDiv.innerHTML = '<span class="section-title">group conversations</span>';
        sidebarDiv.appendChild(conversationsDiv);

        this.elem.appendChild(sidebarDiv);

        this.session.addSideBar(this);
    }

    // render online users
    renderOnlineUsers() {
        this.onlineUsersUl.innerHTML = '';
        let liElements = this.users.filter(user => !!user && user.userId !== this.currentUser.userId)
                                .map(user => this.getUserLi(user));
        for(let li of liElements){
            this.onlineUsersUl.appendChild(li);
        }
    }

    getUserLi(user) {
        let li = document.createElement('li');
        let div = document.createElement('div');
        div.classList.add('contact-card');
        div.innerText = user.username;
        li.appendChild(div);
        let _that = this;
        li.onclick = () => {
            httpGet(PROTOCOL + BASE_URL + '/api/chatroom/v1/' + _that.currentUser.userId + '/' + user.userId)
            .then(response => {
                response.json().then(chatRoomId => {
                    if (!_that.chatBoxes.has(chatRoomId)) {
                        // Add new ChatBox
                        let chatbox = new Chatbox(_that.elem, _that.session, chatRoomId, user.username, _that.chatBoxes.size);
                        chatbox.render();
                        _that.chatBoxes.set(chatRoomId, chatbox);
                    }
                });
            })
            .catch(err => console.error('Error when fetching single chat room: ', err));
        };
        return li;
    }

    search(filter) {
        let li = this.onlineUsersUl.getElementsByTagName('li');
        for (let i = 0; i < li.length; i++) {
            let div = li[i].getElementsByTagName('div')[0];
            let txtValue = div.textContent || div.innerText;
            if (txtValue.toUpperCase().indexOf(filter.toUpperCase()) > -1) {
                li[i].style.display = '';
            } else {
                li[i].style.display = 'none';
            }
        }
    }
}

class Chatbox {

    constructor(elem, session, chatRoomId, title, nbChatBox) {
        this.elem = elem;
        this.session = session;
        this.authorId = session.currentUser.userId;
        this.username = session.currentUser.username;
        this.chatRoomId = chatRoomId;
        this.title = title;
        this.messages = [];
        this.nb = nbChatBox;
    }

    render() {
        this.chatWindow = document.createElement('div');
        this.chatWindow.classList.add('chatbubble', 'opened');
        if (this.nb >= 0) {
            this.chatWindow.classList.add('translateX-' + this.nb % MAX_CHATBOX);
        }

        this.chatHeader = document.createElement('div');
        this.chatHeader.classList.add('unexpanded');
        this.chatHeader.innerHTML = '<div class="title" id="header-title">' + this.title + '</div>';
        this.chatHeader.onclick = () => this.toggleChatWindow();

        this.chatBody = document.createElement('div');
        this.chatBody.classList.add('chat-window', 'expanded');

        this.chatLoader = document.createElement('div');
        this.chatLoader.classList.add('loader-wrapper');
        this.chatLoader.innerHTML = '<div class="loader"><span>{</span><span>}</span></div>';

        this.chatMessages = document.createElement('div');
        this.chatMessages.classList.add('chat-history');
        this.chatMessages.innerHTML = '<ul class="messages clearfix"></ul>';

        this.chatInput = document.createElement('div');
        this.chatInput.classList.add('chat-input');
        this.chatInput.innerHTML = 
            `<div class="flexContainer">
                <textarea class="chat-message-input" rows="1" placeholder="Enter Message"></textarea>
            </div>`;
        
        this.chatInputIcon = document.createElement('div');
        
        // this.chatInput.getElementsByClassName('send-btn')[0].onclick = () => this.onSendCallback();
        let first = true;
        let chatInputTextArea = this.chatInput.getElementsByClassName('chat-message-input')[0];
        chatInputTextArea.onfocus = () => {
            if (first) {
                this.computeBaseScrollHeight(chatInputTextArea);
                first = false;
            }
        };
        chatInputTextArea.oninput = (event) => {
            chatInputTextArea.rows = 1;
            if (event.inputType === 'insertLineBreak') {
                chatInputTextArea.value = '';
            }
            let rows = Math.ceil((chatInputTextArea.scrollHeight - chatInputTextArea.baseScrollHeight) / 16);
            chatInputTextArea.rows = Math.min(rows + 1, 3);
            // Resize chat history paddings 36px
            this.chatMessages.style.height = (this.chatBody.offsetHeight - chatInputTextArea.offsetHeight - 36) + 'px';
            // console.log('Chat window height : ', this.chatBody.offsetHeight, 
            //     'chatInputTextArea height : ', chatInputTextArea.offsetHeight,
            //     'Result : ', this.chatMessages.style.height);
        };

        this.chatInput.onkeydown = (event) => {
            event = event || window.event;
            if ((event.which === 13 || event.keyCode === 13) && !event.shiftKey) {
                this.sendMessage();
            }
        };

        this.chatBody.append(this.chatLoader);
        this.chatBody.append(this.chatMessages);
        this.chatBody.append(this.chatInput);

        this.chatWindow.append(this.chatHeader);
        this.chatWindow.append(this.chatBody);
        this.elem.append(this.chatWindow);

        this.loadingPlaceholder = document.createElement('li');
        this.loadingPlaceholder.classList.add('clearfix', 'mine');
        this.loadingPlaceholder.innerHTML = 
            `<div class="msg-sender">${this.username}</div>
            <div class="msg-placeholder">
                <div id="floatingCirclesG">
                    <div class="f_circleG" id="frotateG_01"></div>
                    <div class="f_circleG" id="frotateG_02"></div>
                    <div class="f_circleG" id="frotateG_03"></div>
                    <div class="f_circleG" id="frotateG_04"></div>
                    <div class="f_circleG" id="frotateG_05"></div>
                    <div class="f_circleG" id="frotateG_06"></div>
                    <div class="f_circleG" id="frotateG_07"></div>
                    <div class="f_circleG" id="frotateG_08"></div>
                </div>
            </div>`;
        
        // Error floating div
        this.errorCard = document.createElement('div');
        this.errorCard.classList.add('overlay-card', 'clickable');
        this.errorCard.onclick =() => this.removeErrorCard();
        this.chatMessages.append(this.errorCard);

        // Get latest messages
        httpGet(PROTOCOL + BASE_URL + '/api/chatroom/v1/' + this.chatRoomId + '/messages')
        .then(response => {
            response.json().then(data => {
                data.results.sort((m1, m2) => m1.sendingDate - m2.sendingDate)
                    .forEach((message) => {
                        // console.log(message);
                        this.newChatMessage(message, 0);
                    });
            });
        })
        .catch(err => console.error('Error when fetching single chat room: ', err));

        this.session.addChatBox(this);

        document.querySelector('.chat-history').addEventListener('mouseenter', function(e){
            e.target.style.overflow = 'auto';
        }, false);
        
        document.querySelector('.chat-history').addEventListener('mouseleave', function(e){
            e.target.style.overflow = 'hidden';
        }, false);
    }

    sendMessage() {
        let chatInputTextArea = this.chatInput.getElementsByClassName('chat-message-input')[0];
        let textMessage = chatInputTextArea.value;
        if (textMessage.length > 0) {
            let message = {
                'chatRoomId': this.chatRoomId,
                'messageId': this.generateUUID(),
                'authorId': this.authorId,
                'authorUsername': this.username,
                'content': textMessage,
            };
            this.session.sendChatMessage('chat-message', message);
            this.newChatMessage(message, 0);
            chatInputTextArea.value = '';
        }
    }

    removeLoader() {
        this.chatLoader.style.display = 'none';
        this.chatMessages.style.display = 'block';
        this.chatInput.style.display = 'block';
    }

    displayLoader() {
        this.chatLoader.style.display = 'block';
        this.chatMessages.style.display = 'none';
        this.chatInput.style.display = 'none';
    }

    removeErrorCard() {
        this.errorCard.style.display = 'none';
    }

    displayErrorCard(errorMessage) {
        this.errorCard.innerHTML = `<span class="error-msg">${errorMessage}</span>`;
        this.errorCard.style.bottom = this.chatInput.offsetHeight + 7;
        this.errorCard.style.display = 'block';
        setTimeout(() => {
            this.removeErrorCard();
        }, 5000);
    }

    toggleChatWindow() {
        this.chatWindow.classList.toggle('opened');
    }

    newChatMessage(message, scrollDelay) {
        const messageClass = this.authorId !== message.authorId ? 'other' : 'mine';

        let newMessage = document.createElement('li');
        newMessage.classList.add('clearfix', messageClass);
        newMessage.innerHTML = `<div class="msg-sender">${message.authorUsername}</div>`;
        if (!!message.content) {
            newMessage.innerHTML += `<div class="msg-content">${message.content}</div>`;
        }
        this.chatMessages.getElementsByClassName('messages')[0].append(newMessage);
        setTimeout(() => {
            this.chatMessages.scrollTop = this.chatMessages.scrollHeight;
            // scrollTo(this.chatMessages, this.chatMessages.scrollHeight, 0);
        }, scrollDelay);
    }
    
    destroy() {
        this.elem.removeChild(this.chatWindow);
    }

    computeBaseScrollHeight(chatInputTextArea) {
        var savedValue = chatInputTextArea.value;
        chatInputTextArea.value = '';
        chatInputTextArea.baseScrollHeight = chatInputTextArea.scrollHeight;
        chatInputTextArea.value = savedValue;
    }

    displayPlaceholder() {
        this.chatMessages.getElementsByClassName('messages')[0].append(this.loadingPlaceholder);
        this.chatMessages.scrollTop = this.chatMessages.scrollHeight;
    }

    removePlaceholder() {
        this.chatMessages.getElementsByClassName('messages')[0].removeChild(this.loadingPlaceholder);
        this.chatMessages.scrollTop = this.chatMessages.scrollHeight;
    }

    generateUUID() {
        var d = new Date().getTime();//Timestamp
        var d2 = (performance && performance.now && (performance.now()*1000)) || 0;//Time in microseconds since page-load or 0 if unsupported
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = Math.random() * 16;//random number between 0 and 16
            if(d > 0){//Use timestamp until depleted
                r = (d + r)%16 | 0;
                d = Math.floor(d/16);
            } else {//Use microseconds since page-load if supported
                r = (d2 + r)%16 | 0;
                d2 = Math.floor(d2/16);
            }
            return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
        });
    }
}

function httpGet(url) {
    var myHeaders = new Headers();
    var options = {
        method: 'GET',
        headers: myHeaders,
        mode: 'cors',
        cache: 'default'
    };
    return fetch(url, options);
}

function httpPost(url, body) {
    var myHeaders = new Headers();
    var options = {
        method: 'POST',
        headers: myHeaders,
        mode: 'cors',
        body: body
    };
    return fetch(url, options);
}
function scrollTo(element, to, duration) {
    var start = element.scrollTop,
        change = to - start,
        currentTime = 0,
        increment = 20;
        
    var animateScroll = function(){
        currentTime += increment;
        var val = Math.easeInOutQuad(currentTime, start, change, duration);
        element.scrollTop = val;
        if(currentTime < duration) {
            setTimeout(animateScroll, increment);
        }
    };
    animateScroll();
}
//t = current time
//b = start value
//c = change in value
//d = duration
Math.easeInOutQuad = function (t, b, c, d) {
    t /= d/2;
      if (t < 1) return c/2*t*t + b;
      t--;
      return -c/2 * (t*(t-2) - 1) + b;
};